import React, { useEffect, useState } from 'react';

const ActComponent = () => {
  const [acoes, setAcoes] = useState('');
  const [submitted, setSubmitted] = useState(false);

  // Funo para salvar os dados em localStorage como JSON
  const salvarDados = () => {
    const dados = JSON.stringify({ acoes });
    localStorage.setItem('acoesData', dados);
  };

  // Funo para carregar os dados do localStorage para o estado do componente
  const carregarDados = () => {
    const dados = localStorage.getItem('acoesData');
    if (dados) {
      const { acoes } = JSON.parse(dados);
      setAcoes(acoes);
      setSubmitted(true); // Define submitted como true ao carregar os dados
    }
  };

  // Efeito para carregar os dados ao montar o componente
  useEffect(() => {
    carregarDados();
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    // Processar os dados aqui, como enviar para um servio ou armazenar no estado global
    console.log('Aes de Melhoria Contnua:', acoes);
    salvarDados(); // Salvar os dados ao clicar no boto "Salvar"
    setSubmitted(true); // Define submitted como true aps o envio do formulrio
  };

  return (
    <div>
      <h2>Fase de Ao (Act)</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Aes de Melhoria Contnua:</label>
          <textarea
            value={acoes}
            onChange={(e) => setAcoes(e.target.value)}
            required
          />
        </div>
        <button type="submit">Salvar</button>
      </form>

      {submitted && (
        <div>
          <h3>Aes de Melhoria Contnua Inseridas:</h3>
          <p>{acoes}</p>
        </div>
      )}
    </div>
  );
};

export default ActComponent;
